import { notFound } from 'next/navigation';
import Link from 'next/link';
import { Suspense } from 'react';

interface BlogPost {
  title: string;
  date: string;
  path: string;
  category: string;
  content: string;
}

interface PageProps {
  params: Promise<{ path: string[] }>;
}

async function fetchBlogPost(path: string): Promise<BlogPost | null> {
  try {
    const response = await fetch(`http://localhost:8080/api/blog-posts/${encodeURIComponent(path)}`, {
      cache: 'no-cache',
      next: { revalidate: 3600 }
    });
    
    if (!response.ok) {
      return null;
    }
    
    return await response.json();
  } catch (error) {
    console.error('Erreur lors du chargement de l\'article:', error);
    return null;
  }
}

function MarkdownRenderer({ content }: { content: string }) {
  // Simple rendu markdown basique
  const renderMarkdown = (text: string) => {
    return text
      // Titres
      .replace(/^### (.*$)/gm, '<h3 class="text-lg font-semibold mt-6 mb-3 text-foreground">$1</h3>')
      .replace(/^## (.*$)/gm, '<h2 class="text-xl font-semibold mt-8 mb-4 text-foreground">$1</h2>')
      .replace(/^# (.*$)/gm, '<h1 class="text-2xl font-bold mt-10 mb-6 text-foreground">$1</h1>')
      // Code inline
      .replace(/`([^`]+)`/g, '<code class="bg-foreground/10 px-1 py-0.5 rounded text-sm font-mono">$1</code>')
      // Code blocks
      .replace(/```([^`]+)```/g, '<pre class="bg-foreground/5 p-4 rounded-lg overflow-x-auto my-4"><code class="text-sm font-mono">$1</code></pre>')
      // Liens
      .replace(/\[([^\]]+)\]\(([^)]+)\)/g, '<a href="$2" target="_blank" class="text-foreground/80 underline decoration-1 underline-offset-4 hover:decoration-2 hover:text-foreground transition-colors">$1</a>')
      // Gras
      .replace(/\*\*([^*]+)\*\*/g, '<strong class="font-semibold">$1</strong>')
      // Italique
      .replace(/\*([^*]+)\*/g, '<em class="italic">$1</em>')
      // Paragraphes
      .replace(/\n\n/g, '</p><p class="mb-4 leading-relaxed text-foreground/90">')
      // Listes
      .replace(/^\- (.*$)/gm, '<li class="ml-4 mb-1">• $1</li>');
  };

  return (
    <div 
      className="prose prose-sm max-w-none"
      dangerouslySetInnerHTML={{ 
        __html: `<p class="mb-4 leading-relaxed text-foreground/90">${renderMarkdown(content)}</p>` 
      }}
    />
  );
}

function LoadingSkeleton() {
  return (
    <div className="animate-pulse">
      <div className="h-8 bg-foreground/10 rounded w-3/4 mb-4"></div>
      <div className="flex gap-4 mb-6">
        <div className="h-4 bg-foreground/10 rounded w-20"></div>
        <div className="h-4 bg-foreground/10 rounded w-24"></div>
      </div>
      <div className="space-y-3">
        <div className="h-4 bg-foreground/10 rounded w-full"></div>
        <div className="h-4 bg-foreground/10 rounded w-full"></div>
        <div className="h-4 bg-foreground/10 rounded w-3/4"></div>
        <div className="h-4 bg-foreground/10 rounded w-full"></div>
        <div className="h-4 bg-foreground/10 rounded w-2/3"></div>
      </div>
    </div>
  );
}

async function BlogPostContent({ path }: { path: string }) {
  const post = await fetchBlogPost(path);

  if (!post) {
    notFound();
  }

  return (
    <article className="max-w-4xl mx-auto">
      <header className="mb-8">
        <div className="flex items-center gap-4 mb-4 text-sm text-foreground/60">
          <span className="bg-foreground/5 px-2 py-1 rounded">
            {post.category}
          </span>
          <time dateTime={post.date}>{post.date}</time>
        </div>
        <h1 className="text-3xl font-bold mb-4 text-foreground">
          {post.title}
        </h1>
      </header>
      
      <div className="prose prose-lg max-w-none">
        <MarkdownRenderer content={post.content} />
      </div>
    </article>
  );
}

export default async function BlogPostPage({ params }: PageProps) {
  const resolvedParams = await params;
  const path = resolvedParams.path.join('/');

  return (
    <div className="pb-16 px-4 mx-auto max-w-6xl">
      <header className="mb-8">
        <div className="flex items-center gap-4 mb-4">
          <Link 
            href="/blog" 
            className="text-foreground/60 hover:text-foreground transition-colors text-sm underline decoration-1 underline-offset-4"
          >
            ← Retour au blog
          </Link>
          <Link 
            href="/" 
            className="text-foreground/60 hover:text-foreground transition-colors text-sm underline decoration-1 underline-offset-4"
          >
            Accueil
          </Link>
        </div>
      </header>
      
      <main>
        <Suspense fallback={<LoadingSkeleton />}>
          <BlogPostContent path={path} />
        </Suspense>
      </main>
    </div>
  );
}
